<template>
  <div class="container">
    <PageTitle :path="require('@/assets/images/page-title3.png')" >五金 - {{ categoryName }}</PageTitle>
    <div class="row mh-wrapper">
      <div class="col-12">
        <Menu :active="'五金'"></Menu>
      </div>
      <div class="col-12 py-3">
        <div class="row">
          <div class="col-sm-6 col-md-4 col-lg-3 mb-4"  v-for="item in productResult" :key="item.id">
            <ProductCard :data="item" :category="category" :categoryMain="'metal'"></ProductCard>
          </div>
        </div>
        <div class="row">
          <div class="col-12 py-3">
            <Pagination :pageItem="pageItem" :data="productFilter" @changePage="goToPage"/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PageTitle from '@/components/Front/PageTitle.vue'
import Menu from '@/components/Front/Menu.vue'
import ProductMixin from '@/mixin/ProductMixin.js'
import ProductCard from '@/components/Front/ProductCard.vue'
import Pagination from '@/components/Share/Pagination.vue'

export default {
  name: 'Metal',
  components: {
    PageTitle,
    Menu,
    ProductCard,
    Pagination
  },
  mixins: [ProductMixin],
  inject: ['emitter'],
  data () {
    return {
      category: '',
      productAll: [],
      productFilter: [],
      productResult: [],
      pageItem: {}
    }
  },
  computed: {
    categoryName () {
      if (this.category === '門鎖門把') {
        return '門鎖/門把'
      } else {
        return this.category
      }
    }
  },
  watch: {
    $route (to, from) {
      const kind = to.params.metal
      this.category = kind
      this.getProductByCategory()
    }
  },
  methods: {
    getProductByCategory (page = 1) {
      switch (this.category) {
        case '絞鍊':
          this.productFilter = this.productAll.filter((item) => item.category === '絞鍊')
          break
        case '滑軌':
          this.productFilter = this.productAll.filter((item) => item.category === '滑軌')
          break
        case '門鎖門把':
          this.productFilter = this.productAll.filter((item) => item.category === '門鎖/門把')
          break
        case '其他':
          this.productFilter = this.productAll.filter((item) => item.category === '其他')
          break
      }
      const result = this.$pagination(this.productFilter, this.productResult, page)
      this.pageItem = result[0]
      this.productResult = result[1]
    },
    goToPage (page) {
      const result = this.$pagination(this.productFilter, this.productResult, page)
      this.pageItem = result[0]
      this.productResult = result[1]
    }
  },
  created () {
    const category = this.$route.params.metal
    this.category = category
    this.emitter.emit('cartVisible', true)
    this.getProductAll()
  }
}
</script>
